import React, { useEffect, useState } from 'react';
import { Avatar } from 'antd';
import classnames from 'classnames';
import { TAppItem, TAppList, BarAppList } from '@/routes/constants';
import { isLightColor } from '@/utils/tool';
import Bar from './bar';
import styles from './index.less';

const Apps: React.FC = () => {
  let carouselRef = React.createRef<any>();
  const list = BarAppList.map((i, index) => {
    i.index = index;
    i.items = i.items.map((o, oindex) => {
      o.index = oindex;
      return o;
    });
    return i;
  });
  const [curItem, setCurItem] = useState<TAppList>(list[0]);
  const handleClickItem = (item: any) => {
    setCurItem(item);
  };

  useEffect(() => {
    carouselRef?.current?.goTo(curItem.index);
  }, [curItem, carouselRef]);

  const gotoUrl = (url: string) => { window.open(url); };

  const itemsCount = list.length;

  return (
    <>
      <div className={styles.appsWrap}>
        <div className={styles.appsPanel}>
          {itemsCount && (
            <div
              className={styles.appsSwiper}
              style={{
                height: `${itemsCount * 100}%`,
                transform: `translateY(-${100 / itemsCount * curItem.index}%)`
              }}
            >
              {list.map(apps => (
                <div className={styles.appSwiper} key={apps.index} style={{ height: `${100 / itemsCount}%` }}>
                  <div className={styles.appPanel}>
                    <div className={styles.appGrid}>
                      {apps.items.map((i: TAppItem) => (
                        <div
                          key={i.index}
                          className={classnames(
                            styles.appItem,
                            i.size === '2x2' && styles.appItem2x2,
                            i.size === '2x4' && styles.appItem2x4,
                          )}
                          onClick={() => i.url && gotoUrl(i.url)}
                        >
                          <Avatar
                            className={styles.appIcon}
                            style={{
                              backgroundColor:  i.backgroundColor || '#c7c7cc',
                              color: i.color || isLightColor(i.backgroundColor || '#c7c7cc') ? '#000' : '#fff'
                            }}
                            draggable={false}
                            src={i.src}
                            alt={i.iconText || i.name}
                          />
                          <div className={styles.appText}>{i.name}</div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>
      <div className={styles.barWrap}>
        <Bar list={list} handleClickItem={handleClickItem} curItem={curItem} />
      </div>
    </>
  );
};

export default Apps;
